import React from 'react';
import {connect} from 'react-redux';

//首页的商品陈列

const Goodshow = (props)=>{
    //console.log(props.data);
    return(
        <div>
            <div style={{width:'46%',height:'auto',backgroundColor:'#fff',float:'left',margin:' 0 2%',marginTop:'3%',borderRadius:'9px'}} 
            onClick={()=>{
                //localStorage.setItem('img',props.data.img)
                //localStorage.setItem('id',props.data.id)
                //localStorage.setItem('title',props.data.title)
                //localStorage.setItem('price',props.data.price)
                localStorage.setItem('data',JSON.stringify(props.data))
                props.history.push('./home/goods')}
            }>
                <div style={{width:'100%',height:'58%',overflow:'hidden',borderTopLeftRadius:'9px',borderTopRightRadius:'9px'}}>
                    <img src={props.data.img} alt="" style={{width:'100%',height:'100%'}}></img>
                </div>
                <div style={{height:'80px',paddingLeft:'10px',paddingRight:'10px',overflow:'hidden',marginTop:'-3px',borderBottomLeftRadius:'9px',borderBottomRightRadius:'9px'}}>
                    <div style={{marginTop:'15px',whiteSpace:'nowrap',lineHeight:'100%',overflow:'hidden'}}>
                        <div style={{height:'30px'}}>{props.data.title}</div>
                    </div>
                    <div style={{marginBottom:'5%'}}>
                        <span>￥</span>
                        <span style={{marginRight:'10%'}}>{props.data.price}</span>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default connect(
    store=>{return store;}
)(Goodshow);