
const login = (username)=>{
    return (dispatch)=>{
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login';
        fetch(url, {
            method: 'post',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then(res => res.json())
            .then(res => {
                dispatch({
                    type: 'LOGIN',
                    userinfo: res.data.name
                })
            })
    }
}


const getList = ()=>{
    return(dispatch)=>{
        let url= 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then(res => res.json())
            .then(res => {
                dispatch({
                    type: 'GETLIST',
                    gooddata: res.data
                })
            })
    }
}


function collect(data){
    return{
        type: 'COLLECT',
        collectData: data
    }
}

function del(idx){
    return({
        type: 'DELETE',
        id:idx
    })
}

export {login, getList, collect, del}