import React from 'react';
import { NavLink } from 'react-router-dom';
import Carou from './Carou';
import {login,getList} from './actionCreators';
import {connect} from 'react-redux';
import Goodshow from './Goodshow';

// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>

const Home = (props)=>{
    // props.dispatch(login());
    props.dispatch(getList());
    //console.log(props);
        return (
            <div style={{backgroundColor:'#f2f2f2'}}>
                <div style={{backgroundColor:'rgb(255, 133, 42)',height:'38px',width:'100%',paddingTop:'9px',position:'fixed',top:'0',zIndex:'100'}}>
                    <i className="iconfont icon-taobao" style={{float:'left',color:'white',fontSize:'28px',paddingLeft:'8px'}}></i>
                    <NavLink to='/home/search' style={{float:'right',width:'85%',height:'30px',display:'block',backgroundColor:'rgb(255,78,34)',textAlign:'center',borderRadius:'4px',marginRight:'2%'}}>
                        <span className='iconfont icon-search' style={{color:'white',lineHeight:'30px',fontSize:'12px'}}>寻找宝贝店铺</span>
                    </NavLink>
                </div>

                {/*走马灯*/}
                <div style={{marginTop:'47px'}}>
                <Carou />
                </div>

                <div style={{width:'100%',height:'60px',color:'rgb(255, 133, 42)',textAlign:'center',lineHeight:'60px'}}>
                    <span className='iconfont icon-xihuan' style={{marginRight:'5px'}}></span>
                    <span style={{fontSize:'12px'}}>你可能还喜欢</span>
                </div>

                {/*商品陈列*/}
                <div style={{minHeight:'1300px',marginBottom:'100px'}}>
                    {
                        props.datalist.map((items,id)=>{
                            return <Goodshow data={items}  history={props.history} key={id}/>
                        })
                    }
                </div>
            
            </div>
            
        )
    
}

export default connect(
    store=>{return store;}
)(Home);
