import React from 'react';
import {connect} from 'react-redux';
import {del} from './actionCreators';

//收藏页里的每一个商品的组件

const Show = (props)=>{
    //console.log(props);
    return(
        <div style={{width:'95%',height:'145px',background:'red',margin:'0 auto',marginTop:'20px',borderRadius:'4%',backgroundColor:'#fff'}}>
            <div style={{width:'40%',height:'auto',float:'left',overflow:'hidden'}}>
                <img src={props.data.img} style={{width:'auto',height:'auto',maxWidth:'100%',maxHeight:'100%',borderRadius:'4%',}}></img>
            </div>
            <div style={{width:'60%',height:'150px',float:'left',borderRadius:'4%'}}>
                <div style={{height:'30px',marginTop:'30px',marginLeft:'20px',fontSize:'20px'}}>
                    <span>{props.data.title}</span>
                </div>
                <div style={{marginTop:'30px',marginLeft:'20px',fontSize:'20px'}}>
                    <span style={{marginRight:'10px'}}>￥</span>
                    <span>{props.data.price}</span>
                    <input type='button' value='取消收藏' style={{float:'right',marginRight:'10px'}} onClick={()=>props.dispatch(del(props.data.id))}></input>
                </div>
            </div>
        </div>
    )
}

export default connect(
    store=>{return store;}
)(Show);