let data={
    datalist:[],
    shoucang:[],
    userinfo:'',
}

const homereducer = (state=data,action) =>{
    switch(action.type){
        case 'LOGIN':
            if(state.userinfo==''){
                return Object.assign({},state,{userinfo:action.userinfo});
            }else{
                return state;
            }
        case 'GETLIST':
            return Object.assign({},state,{datalist:action.gooddata});
        case 'COLLECT':
            if(state.shoucang.every(item=>{
                return item.id !== action.collectData.id;
            })){
                return Object.assign({},state,{shoucang:[...state.shoucang,action.collectData]});
            }else{
                return state;
            }
        case 'DELETE':
            let arr = state.shoucang.filter(item=>{
                //console.log(item);
                //console.log(action.id);
                if(item.id==action.id){
                    return false;
                }else{
                    return true;
                }
            })
            return Object.assign({},state,{shoucang:arr});
        default:
            return state;
    }
}

export default homereducer;