import React from 'react';
import { login } from './actionCreators';
import {connect} from 'react-redux';

const Login = (props)=>{
    console.log(props);
    return(<div>
        {
            props.userinfo 
            ? props.history.goBack()
            : <div>
                    <div style={{color:'#ff5000',fontSize:'50px',margin:'50px',textAlign:'center'}}>
                        <span style={{margin:'0 10px'}}>淘</span>
                        <span style={{margin:'0 10px'}}>宝</span>
                    </div>
                    <div style={{textAlign:'center'}}>
                        <input type="text" style={{width:'90%',height:'40px',margin:'20px 0',backgroundColor:'#f5f5f9',border:'0px',borderBottom:'1px solid #ff9000'}} placeholder='手机号/邮箱/用户名'></input>
                        <br/>
                        <input type="password" style={{width:'90%',height:'40px',margin:'20px 0',backgroundColor:'#f5f5f9',border:'0px',borderBottom:'1px solid #ff9000'}} placeholder='请输入登录密码'></input>
                        <button onClick={()=>props.dispatch(login())} style={{width:'60%',height:'45px',margin:'20px 0',color:'#fff',background:'-webkit-linear-gradient(left,#ff9000,#ff5000) no-repeat',borderRadius:'35px',boxShadow:'0 2px 4px #f7c7b1'}}>登录</button>
                    </div>
            </div>
        }

    </div>

    )
}

export default connect(
    store=>{return store;}
)(Login);