import React from 'react';
import ReactDom from 'react-dom';
import { Link } from 'react-router-dom';
import {connect} from 'react-redux';
import {collect} from './actionCreators';

//点进来的具体的商品详情页


const Goods = (props)=>{
    //console.log(props);
    let data = JSON.parse(localStorage.getItem('data'));
    let a ='iconfont icon-shoucang';

    for(let i=0;i<props.shoucang.length;i++){
        //console.log(props.shoucang[i].id);
        //console.log(data.id);
        if(props.shoucang[i].id==data.id){
           console.log('have');
           i = props.shoucang.length + 1;
           //var myimg = document.getElementById('myimg');
           //ReactDom.findDOMNode(myimg).className="iconfont icon-shoucang1"
            a = 'iconfont icon-shoucang1';
        }
    } 

    return(
        <div>
            <div style={{width:'100%'}}>
                <div style={{position:'absolute',width:'100%',overflow:'hidden'}}>
                    <img src={data.img}></img>
                </div>
                <div onClick={()=>props.history.push('/')}  style={{width:'40px',height:'40px',borderRadius:'50%',backgroundColor:'rgb(200,200,200,0.5)',position:'relative',left:'10px',top:'20px',display:'block'}}>
                    <i className='iconfont icon-fanhui' style={{color:'#fff',position:'relative',left:'10px',top:'10px'}}></i>
                </div>
            </div>
            <div style={{position:'fixed',width:'100%',height:'50px',bottom:'0',display:'flex',hmaxWidth:'768px',zIndex:'110',flexDirection:'row',padding:'0 0.2rem'}}>
                <div style={{width:'40%',height:'50px',display:'flex',borderTop:'1px solid #000',backgroundColor:'#fff'}}>
                    <div style={{flex:'1'}}>
                        <Link to='/dianpu'><i className='iconfont icon-dianpu' style={{color:'rgb(255, 133, 42)',fontSize:'20px',lineHeight:'30px',display:'block',margin:'0 20%'}}></i><span style={{fontSize:'14px',color:'#000',margin:'0 15%'}}>店铺</span></Link>
                    </div>
                    <div style={{flex:'1'}}>
                        <Link to='/kefu' style={{color:'#000'}}><i className='iconfont icon-kefu' style={{color:'rgb(255, 133, 42)',fontSize:'20px',lineHeight:'30px',display:'block',margin:'0 20%'}}></i><span style={{fontSize:'14px',color:'#000',margin:'15%'}}>客服</span></Link>
                    </div>
                    <div style={{flex:'1'}}>
                        <div  onClick={()=>{
                            if(props.userinfo==''){props.history.push('/login');}
                            props.dispatch(collect(data));
                            }}
                            style={{color:'#000'}}><i id="myimg" className={a} style={{color:'rgb(255, 133, 42)',fontSize:'20px',lineHeight:'30px',display:'block',margin:' 0 20%'}}></i><span style={{fontSize:'14px',color:'#000',margin:'15%'}}>收藏</span>
                        </div>
                    </div>
                </div>
                <div style={{width:'60%',height:'50px',display:'flex',fontSize:'15px'}}>
                    <div style={{flex:'1',backgroundColor:'#ff9500',borderRadius:'5px',lineHeight:'50px',textAlign:'center'}}>
                        <Link to='/cart' style={{color:'#fff'}}><span>加入购物车</span></Link>
                    </div>
                    <div style={{flex:'1',backgroundColor:'#ff0036',borderRadius:'5px',lineHeight:'50px',textAlign:'center'}}>
                        <Link to='/order' style={{color:'#fff'}}><span>立即购买</span></Link>
                    </div>
                </div>
            </div>
        </div>
        
    )
}


// function changeColor(e){
//     e.currentTarget.className='iconfont icon-shoucang1'
//     e.currentTarget.style.color='rgb(255, 133, 42)'
// }


export default connect(
    store=>{return store;}
)(Goods);