import React from 'react';
import {connect} from 'react-redux';
import Show from './Show';

//首页点进去的收藏

const Collect = (props) =>{
    //console.log(props);
    return(
        <div style={{marginBottom:'65px'}}>
            {
                props.shoucang.map((data,id)=>{
                    return <Show data={data} key={id}/>
                })
            }
        </div>
        
        
    )
}

export default connect(
    store=>{return store;}
)(Collect);